// src/data/teacherScheduleData.js
import {bellSchedule} from "./scheduleData";

//   ( ..)
const teacherSchedule = {
    monday: [
        { id: "3", subject: "", class: "10-" },
        { id: "4", subject: "", class: "10-" },
        { id: "5", subject: "", class: "9-" },
        { id: "6", subject: "", class: "9-" },
    ],
    tuesday: [
        { id: "3", subject: "", class: "10-" },
        { id: "4", subject: "", class: "10-" },
        { id: "7", subject: "", class: "10-" },
        { id: "8", subject: "", class: "10-" },
    ],
    wednesday: [
        { id: "1", subject: "", class: "10-" },
        { id: "2", subject: "", class: "10-" },
        { id: "3", subject: "", class: "11-" },
        { id: "4", subject: "", class: "11-" },
    ],
    // ...  ..
    friday: [
        { id: "5", subject: "", class: "10-" },
        { id: "6", subject: "", class: "10-" },
    ]
};

export const getTeacherScheduleForDate = (date) => {
    const dayOfWeek = date.getDay();
    if (dayOfWeek === 0 || dayOfWeek === 6) {
        return []; // 
    }

    const dayNames = ['sunday', 'monday', 'tuesday', 'wednesday', 'thursday', 'friday', 'saturday'];
    const dayName = dayNames[dayOfWeek];

    const lessons = teacherSchedule[dayName] || [];

    return lessons.map(lesson => {
        const schedule = bellSchedule[Number(lesson.id) - 1];
        return {
            ...lesson,
            lessonNumber: lesson.id,
            time: schedule ? `${schedule.start} - ${schedule.end}` : "N/A"
        };
    });
};