export const newsData = [
    {
        id: 1,
        title: " ",
        text: " !  '  18:00    Zoom.",
        date: "2025-11-01",
        author: " "
    },
    {
        id: 2,
        title: "  ",
        text: "    09:00.    .",
        date: "2025-10-28",
        author: ""
    }
];

export const addNews = (title, text) => {
    const newPost = {
        id: Date.now(),
        title,
        text,
        date: new Date().toISOString().split('T')[0],
        author: " .."
    };
    //    
    newsData.unshift(newPost);
    return newsData;
};